import React from 'react';
import Modal from '@material-ui/core/Modal';
import TextField from '@material-ui/core/TextField';

import login from '../assets/login.png';
import signup from '../assets/signup.png';
import './LandingModal.css'

class LandingModal extends React.Component {
  state = {
    open: this.props.showLoginModal,
  };

  handleClose = () => {
    this.props.toggleModal();
  };

  componentDidUpdate(prevProps) {
    if (this.props !== prevProps) {
      this.setState({ open: this.props.showModal });
    }
  }

  render() {
    const image = this.props.type === "login" ? login : signup;

    return (
      <div>
        <Modal
          open={this.state.open}
          onClose={this.handleClose}
          className="modal-container"
        >
          <div className="modal">
            <img src={image} className="modal-heading" alt="modal-heading" />
            <form className="modal-form">
              <TextField
                label="Username"
                className="input"
                value={this.state.username}
                margin="normal"
                variant="outlined"
              />

              <TextField
                label="Password"
                className='input'
                value={this.state.password}
                // onChange={this.handleChange('name')}
                margin="normal"
                variant="outlined"
              />

              <button className="button secondary-button" onClick={this.toggleLoginModal}>
                Submit
              </button>
            </form>
            <div class="fb-login-button" data-width="" data-size="medium" data-button-type="login_with" data-auto-logout-link="false" data-use-continue-as="true"></div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default LandingModal;